a {
  text-decoration: none;
  color: unset;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.fw-bold {
  font-weight: bold;
}

body {
  background-color: #d5e6e6;
  font-family: Helvetica, Arial, sans-serif;
}

h1 {
  font-weight: 400;
  margin: 0;
}

.link {
  color: black;
  font-size: 2rem;
}
.link:hover {
  font-style: italic;
}

.arrow {
  display: inline-block;
  font-style: normal !important;
  height: 43px;
  left: -1px;
  position: relative;
  transform: rotate(135deg);
  width: 35px;
}

.container {
  width: 95%;
  display: flex;
  margin: 2rem auto 2rem auto;
}
@media (max-width: 600px) {
  .container {
    margin-top: 1rem;
  }
}

.main-content {
  width: 60%;
  border: 1px solid #707070;
  position: relative;
  padding: 8px 8px 0px 8px;
}
@media (max-width: 600px) {
  .main-content {
    width: 100%;
  }
}

.site-title {
  font-size: 4.5rem;
  text-transform: uppercase;
}
@media (max-width: 600px) {
  .site-title {
    font-size: 3rem;
  }
}

.site-subtitle {
  position: absolute;
  top: 95px;
  font-size: 2rem;
  text-transform: initial;
  left: 500px;
}
@media (max-width: 600px) {
  .site-subtitle {
    display: block;
    position: static;
  }
}

.link-wrapper {
  margin-top: 4rem;
  margin-bottom: 4rem;
}
@media (max-width: 600px) {
  .link-wrapper {
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
}

.link-icon {
  position: relative;
  text-decoration: none;
  display: inline-block;
}
.link-icon__text {
  bottom: 5px;
  color: #000;
  position: relative;
  font-size: 2rem;
}
.link-icon__icon {
  width: 50px;
}
.link-icon:hover .link-icon__text {
  font-style: italic;
}
.link-icon:hover .link-icon__icon {
  transform: skew(-8deg);
}

.image-wrapper {
  width: 40%;
  border: 1px solid #707070;
  border-left: none;
  display: flex;
  align-items: center;
}
.image-wrapper__image {
  width: 100%;
  height: 565px;
  object-fit: contain;
  object-fit: contain;
}
@media (max-width: 600px) {
  .image-wrapper {
    display: none;
  }
}

.featured-text {
  font-size: 3rem;
  font-style: italic;
}

.contact-box {
  border-top: 1px solid #707070;
  display: flex;
  justify-content: space-between;
  margin-left: -8px;
  padding: 8px;
  font-size: 1.25rem;
  margin-top: 8px;
  flex-wrap: wrap;
  width: 100%;
}
.contact-box__text {
  text-transform: uppercase;
}

.headshot-mobile {
  display: none;
  width: 100%;
  height: auto;
  object-fit: contain;
  margin-top: 8px;
}
@media (max-width: 600px) {
  .headshot-mobile {
    display: block;
  }
}